import { createContext, useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "@connections/NovaConnection";

import { deleteToken, setToken } from "@utils/Cookie";

const INVALID_USER = {
  valid: false,
};

const AuthContext = createContext({
  user: null,
  login: () => {},
  logout: () => {},
  getUser: () => {},
  updateUser: () => {},
});

export function AuthProvider({ children }) {
  const [user, setUser] = useState(null);

  const getUser = async () => {
    try {
      const response = await axios.get("/user/whoami/");
      if (!response.data.userInfo) {
        throw new Error("Cannot find user data");
      }

      setUser({
        valid: true,
        ...response.data.userInfo,
      });
    } catch {
      setUser(INVALID_USER);
      throw new Error(
        "      .     ."
      );
    }
  };

  const login = async ({ id, password }) => {
    try {
      const response = await axios.login({
        id,
        password,
      });
      const { token } = response.data;
      setToken(token);
      await getUser();
    } catch (error) {
      if (error?.response?.status === 403) {
        throw new Error("    .");
      } else {
        throw new Error(
          "   .     ."
        );
      }
    }
  };

  const logout = () => {
    setUser(INVALID_USER);
    deleteToken();
  };

  async function updateUser(name, value) {
    setUser((prevUser) => ({
      ...prevUser,
      [name]: value,
    }));
  }

  useEffect(() => {
    (async () => {
      try {
        await getUser();
      } catch {}
    })();
  }, []);

  return (
    <AuthContext.Provider
      value={{
        user,
        login,
        logout,
        getUser,
        updateUser,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth(loginAuthRequired) {
  const context = useContext(AuthContext);
  const navigate = useNavigate();

  if (!context) {
    throw new Error(" AuthProvider   .");
  }

  useEffect(() => {
    if (loginAuthRequired && context.user && !context.user.valid) {
      navigate("/login");
    }
  }, [loginAuthRequired, context.user, navigate]);

  return context;
}
