import { Input} from "../../Components";

const FormSchema =  [
    {
        key: "id",
        label: "",
        component: Input,
        property: {
            type: "text",
            placeholder: "",
        },
        validate: ({value})=>{
            return (value.length>0)?
            {
                result: true
            }:
            {
                result: false,
                message: "    ."
            }
        }
    },
    {
        key: "password",
        label: "",
        component: Input,
        property: {
            type: "password",
            placeholder: "",
            sideButtonLabel: "Show"
        },
        validate: ({value})=>{
            return (/^(?=.*[a-zA-Z])(?=.*[0-9])/.test(value) && value.length>=8)?
            {
                result: true
            }:
            {
                result: false,
                message: "    8 ."
            }
        },
        handlers: {
            onSideButtonClick: ({key, value}, controller)=>{
                controller.setField(key, "property", (property)=>{
                    return (property.type==="password")?
                        {
                            ...property,
                            type: "text",
                            sideButtonLabel: "Hide"
                        }:
                        {
                            ...property,
                            type: "password",
                            sideButtonLabel: "Show"
                        }
                });
            }
        }
    },
    {
        key: "passwordRetype",
        label: "",
        component: Input,
        property: {
            type: "password",
            placeholder: " ",
            sideButtonLabel: "Show"
        },
        validate: ({value}, controller)=>{
            return (value === controller.getValue("password"))?
            {
                result: true
            }:
            {
                result: false,
                message: "  ."
            }
        },
        watch: ["password"],
        handlers: {
            onSideButtonClick: ({key, value}, controller)=>{
                controller.setField(key, "property", (property)=>{
                    return (property.type==="password")?
                        {
                            ...property,
                            type: "text",
                            sideButtonLabel: "Hide"
                        }:
                        {
                            ...property,
                            type: "password",
                            sideButtonLabel: "Show"
                        }
                });
            }
        }
    },
]

export default FormSchema;